import base from './base'; // 导入接口域名列表
import axios from './../request/http'; // 导入http中创建的axios实例
import qs from 'qs'; // 根据需求是否导入qs模块

const registerApi = {    
   
    // 手机号码
    telephoneCode (params) { 
        return axios.post(`${base.baseApi}/Modules/UserInfo/GainCode?${qs.stringify(params)}`);    
    },
    // 注册
    userRegister(params){
        return axios.post(`${base.baseApi}/Modules/UserInfo/Register?${qs.stringify(params)}`);    
    },

    // userLogin (params) {        
    //     return axios.post(`${base.baseApi}/Modules/UserInfo/UserLogin`, qs.stringify(params));    
    // }
    //登陆
    userLogin(params){
        return axios.post(`${base.baseApi}/Modules/UserInfo/UserLogin?${qs.stringify(params)}`)
    },
    //登出
    userLoginOut(params){
        return axios.post(`${base.baseApi}/Modules/UserInfo/UserLoginOut?${qs.stringify(params)}`)
    },

    //修改密码--登陆状态下
    editPassword(params){
        return axios.post(`${base.baseApi}/Modules/UserInfo/ChangePassword?${qs.stringify(params)}`)
    },
    //重置密码--未登陆状态下
    resetPassword(params){
        return axios.post(`${base.baseApi}/Modules/UserInfo/ResetPassword?${qs.stringify(params)}`)
    },

    // 其他接口…………
}

export default registerApi;
