import base from './base'; // 导入接口域名列表
import axios from './../request/http'; // 导入http中创建的axios实例
import qs from 'qs'; // 根据需求是否导入qs模块

const userInfoApi = {    
   
   
    //获取用户信息
    getUserInfo(params){
        let config = {
            headers:{
            'content-type':'application/json',
            'token':localStorage.getItem('token')
            }
        }
        return axios.post(`${base.baseApi}/Modules/UserInfo/UserInfo?${qs.stringify(params)}`,params,config)
    },
    //修改用户信息
    UpdateUser(params){
        let config = {
            headers:{
            'content-type':'application/json',
            'token':localStorage.getItem('token')
            }
        }
        return axios.post(`${base.baseApi}/Modules/UserInfo/UpdateUser?${qs.stringify(params)}`,params,config)
    },

    //绑定英航卡
    bindCard(params){
        return axios.post(`${base.baseApi}/Modules/UserInfo/BindCard?${qs.stringify(params)}`)
    },

    //解绑银行卡
    UntyingCard(params){
        return axios.post(`${base.baseApi}/Modules/UserInfo/UntyingCard?${qs.stringify(params)}`)
    },
    //我的收益
    
    MyProfit(params){
        return axios.post(`${base.baseApi}/Modules/UserInfo/MyProfit?${qs.stringify(params)}`)
    },
    //我的订单
    MyOrder(params){
        return axios.post(`${base.wApi}/Modules/OrderInfo/Order?${qs.stringify(params)}`)
    },
    // 车位管理列表
    ParkingInfo(params){
        return axios.post(`${base.wApi}/Modules/ParkingInfo/Parking?${qs.stringify(params)}`)
    },

    // 其他接口…………
}

export default userInfoApi;
