import base from './base'; // 导入接口域名列表
import axios from './../request/http'; // 导入http中创建的axios实例
import qs from 'qs'; // 根据需求是否导入qs模块

const map = {    
    //楼盘列表/关键词搜索/定位周边楼盘
    Buildings(params){
        return axios.post(`${base.baseApi}/Modules/SourceInfo/Buildings?${qs.stringify(params)}`)
    },
    // 其他接口…………
}

export default map;
