<template>
  <div class="cathelment">
    <el-scrollbar>
      <el-container>
        <el-header height="40px">
          <!-- 头部 -->
            <el-row class="home_header meiggin">
              <el-col :span="5"><i class="el-icon-phone"></i><span>客服电话：400000000</span></el-col>
              <div style="float:right" v-show="tokenHavenone">
                <el-col class="active">
                  <button @click="goLogin" type="text">登陆</button>
                </el-col>
                <el-col class="active">
                    <button @click="goRegister" type="text">注册</button>
                  </el-col>
              </div>
              <div style="float:right" v-show="userToken">
                <el-col class="active" style="width:auto;">
                  <router-link to="/userInfo/myserInfo"><button>{{RealName}}</button></router-link>
                </el-col>
                <el-col class="active">
                    <button @click="deleUser">注销</button>
                  </el-col>
              </div>
            </el-row>
        </el-header>
        <el-main style="padding:0;background: #fff;">
          <!-- 导航 -->
          <el-row class="meiggin home_main">
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect" active-text-color="#434a54"  :router='true'>
              <img src='./../../public/img/icons/apple-touch-icon-60x60.png'/>
              <el-cascader
                ref="cascaderAddr"
                :options="options"
                change-on-select
                :props="props"
                placeholder='请选择当前所在地' 
                v-model="selectedOptions"
                @change='areaSelected'
                @visible-change='areaChange'
              ></el-cascader>
              <el-menu-item index="/my" style="float:right" disabled >关于我们</el-menu-item>          
              <el-menu-item index="/map" style="float:right">电子地图</el-menu-item>
              <el-menu-item index="/" style="float:right">首页</el-menu-item>
            </el-menu>
          </el-row>
        </el-main>

      </el-container>
    </el-scrollbar>
  </div>
</template>

<script>
  export default {
    name: 'catHelmet',
    inject:['reload'],
    data() {
        return {
            vals:[],
            selectedOptions: [],
            activeIndex: '',
            tokenHavenone:true,
            userToken:false,
            options: [],
            RealName:'',
            path:'',
            props: {
              label:'AreaName',
              value: 'AreaId',
              // 
              children:'SubsetList'
            }
        }
	  },
    mounted(){
      if(localStorage.RealName){
        let RealName = localStorage.getItem('RealName')
        RealName = RealName.replace("\"","").replace("\"","");
        this.RealName = RealName
      }
      if(localStorage.areaValue){
        let areaValue = localStorage.getItem('areaValue')
        areaValue = areaValue.split(",")
        this.selectedOptions = areaValue
      }
    },
    methods: {
      getData(){
        this.$api.homeApi.AreaZone(
        ).then(res=>{
          this.options = res.data.data
        })
      },
      handleSelect(index) {
        this.activeIndex = index
      },
      goRegister(){
          this.$router.push('/register')
          this.activeIndex =''
      },
      goLogin(){
          this.activeIndex =''
          this.$router.push('/login')
      },
      deleUser(){
        
        this.$api.registerApi.userLoginOut(
          {
            LoginMark:'1234567890',
            Token:localStorage.getItem('token')
          }
        
        ).then(res=>{
          console.log('登出成功')
          if(res.data.code=='200'){
            this.$message({
                showClose: true,
                message: '已退出',
                type: 'success' 
            });
          }
          
        })
        localStorage.clear()
        this.tokenHavenone = true
        this.userToken = false
        this.$router.push('/home')
       
      },
      areaSelected(value){
        window.localStorage["areaValue"] = value;
        let labels = this.$refs['cascaderAddr'].currentLabels
        labels = labels.join()
        window.localStorage["areaLabels"] = labels;
        // this.$router.go(0)
      },
      areaChange(e){
        if(e==false){
          if(this.path=="/map"){
            this.$router.push('./supplierAllBack')
          }
        }
      }
    },
    created(){
      this.getData()
      if(localStorage.getItem('token')){
        this.tokenHavenone = false
        this.userToken = true
      }
      if(!localStorage.getItem('token')){
        this.tokenHavenone = true
        this.userToken = false
      }
      this.activeIndex = this.$route.path
      this.path = this.$route.path
    },
    watch: {
      $route: {
        handler: function(val, oldVal){
            this.activeIndex = this.$route.path
            this.activeIndex = ''
            this.reload()
            this.path = this.$route.path
        },
        deep: true
      }
    },
  }
</script>

<style>
  @import './../components/style/index.scss';
.cathelment .el-scrollbar .el-scrollbar__wrap {
  height: 138px;
}
.cathelment .el-main .el-menu img{
  width: 60px;
  float: left;
}
.cathelment .el-cascader{
      float: left;
      margin: 11px 0 0 10px;
}
