import axios from 'axios';
import router from './../router';
import store from './../store';
import { Message } from 'element-ui'

/** 
 * 提示函数 
 * 禁止点击蒙层、显示一秒后关闭
 */
const tip = msg => {
    Message({
        message: msg,
        duration: 1000,
        onClose: function() {
            reject()
        }
    });
}

